$primaryColor: #4527a4;

// Dashicon
.dashicons {
	font-family: dashicons !important;
}

@for $i from 1 through 12 {
	.columns-#{$i} {
		grid-template-columns: repeat(#{$i}, minmax(240px, 1fr));
	}
}

@media (max-width: 768px) {
	@for $i from 1 through 12 {
		.columns-tablet-#{$i} {
			grid-template-columns: repeat(#{$i}, minmax(240px, 1fr));
		}
	}
}

@media (max-width: 576px) {
	@for $i from 1 through 12 {
		.columns-mobile-#{$i} {
			grid-template-columns: repeat(#{$i}, minmax(240px, 1fr));
		}
	}
}

// Animations
@keyframes loaderBar {
	0% {
		transform: scaleX(0);
		transform-origin: 0 0;
	}

	50% {
		transform: scaleX(1);
		transform-origin: 0 0;
	}

	50.05% {
		transform-origin: 100% 100%;
	}

	100% {
		transform: scaleX(0);
		transform-origin: 100% 100%;
	}
}

// Image Gallery
.wp-block-bigb-image-gallery {
	* {
		box-sizing: border-box;
	}

	.bigbImageGallery {
		.galleryItems {
			display: grid;
		}

		.galleryItem {
			box-sizing: border-box;
			overflow: hidden;
			border-radius: 4px;
			cursor: pointer;
			transition: box-shadow 1s, height 1.5s, transform 1.25s;
			will-change: box-shadow, height, transform;

			&:hover,
			&.bigbNowEditing {
				transform: scale(1.05);
			}

			&:hover .galleryImages img,
			&.bigbNowEditing .galleryImages img {
				// transform: translateX(-12.5%) translateY(-7.5%) scale(1.1);
				transform: scale(1.1);
			}

			&:hover .galleryHeader,
			&.bigbNowEditing .galleryHeader,
			&:hover .galleryHeader::before,
			&.bigbNowEditing .galleryHeader::before {
				opacity: 1;
			}

			&:hover .galleryHeader,
			&.bigbNowEditing .galleryHeader,
			&:hover .galleryHeader h3,
			&.bigbNowEditing .galleryHeader h3,
			&:hover .galleryHeader h5,
			&.bigbNowEditing .galleryHeader h5 {
				transform: translateX(0);
			}

			&:hover .galleryHeader::before,
			&.bigbNowEditing .galleryHeader::before {
				transform: skew(-15deg) translateX(0);
			}
		}

		.galleryLoader {
			display: inline-block;
			opacity: 0;
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translateX(-50%) translateY(-50%);
			overflow: hidden;
			transition: opacity 1s;
			will-change: opacity;

			&.visible {
				opacity: 1;
			}

			.bar {
				display: block;
				width: 32px;
				height: 4px;
				background-color: $primaryColor;
				margin-bottom: 4px;
				animation: loaderBar 2s ease-in-out alternate infinite;
				transform: translateX(-100%);

				@for $i from 1 through 5 {
					&:nth-child(#{$i}) {
						animation-delay: $i * 0.1s;
					}
				}
			}
		}

		.galleryImages {
			width: 100%;
			height: 100%;
			opacity: 0;
			position: absolute;
			top: 0;
			left: 0;
			transform-origin: 50% 50%;
			transition: opacity 1s;
			will-change: opacity, transform;

			&.galleryImagesLoaded {
				opacity: 1;
			}

			img {
				display: block;
				width: 100%;
				max-width: 100%;
				height: 100%;
				max-height: 100%;
				opacity: 0;
				position: absolute;
				top: 0;
				left: 0;
				object-fit: cover;
				// transform: translateX(-12.5%) translateY(-7.5%);
				transform-origin: 50% 50%;
				transition: opacity 1s, transform 1s;
				pointer-events: none;
				will-change: opacity, transform;

				&.imageVisible {
					opacity: 1;
					pointer-events: auto;
				}
			}
		}

		.galleryHeader {
			display: inline-flex;
			align-items: center;
			opacity: 0;
			padding: 16px 40px 16px 16px;
			transform: translateX(-8px);
			transition: opacity 1s, transform 1s;
			will-change: opacity, transform;

			&::before {
				content: "";
				display: block;
				width: 100%;
				height: 100%;
				opacity: 0;
				position: absolute;
				top: 0;
				left: 0;
				border-bottom-right-radius: 8px;
				transform: skew(-15deg) translateX(-32px);
				transform-origin: 0 0;
				transition: opacity 1s, transform 1s;
				transition-delay: 0.3s;
				will-change: opacity, transform;
			}

			.galleryHeadings {
				margin-right: 32px;
			}

			h3,
			h5 {
				position: relative;
				margin: 0;
				text-shadow: 0 0 4px #000;
				transform: translateX(-8px);
				transition: transform 1s;
				will-change: transform;
			}

			h5 {
				transition-delay: 0.2s;
			}

			.btnClose {
				width: 24px;
				height: 24px;
				line-height: initial;
				padding: 2px;
				border: 0;
				border-radius: 50%;
				transition: opacity .5s;
				cursor: pointer;
				will-change: opacity;

				&:active {
					transform: scale(0.9);
				}
			}
		}

		.galleryFooter {
			position: absolute;
			right: 0;
			bottom: 0;
			padding: 16px 16px 16px 25px;
			transform: translateX(8px);
			transition: transform .5s;
			will-change: transform;

			&::before {
				content: "";
				display: block;
				width: 100%;
				height: 100%;
				position: absolute;
				top: 0;
				right: 0;
				border-top-left-radius: 8px;
				transform: skew(-15deg);
				transform-origin: 100% 100%;
			}

			nav.controls {
				display: flex;
				align-items: center;

				.control {
					line-height: initial;
					position: relative;
					padding: 0;
					border: 0;
					border-radius: 50%;
					margin-right: 4px;
					cursor: pointer;

					&:last-child {
						margin-right: 0;
					}
				}

				.controlDot {
					height: 12px;
					width: 12px;
					position: relative;

					&:active {
						transform: scale(0.9);
					}

					&::after {
						content: "";
						display: block;
						width: 50%;
						height: 50%;
						opacity: 0;
						position: absolute;
						top: 50%;
						left: 50%;
						border-radius: 50%;
						transform: translate(-50%, -50%);
						transition: opacity 1s;
						will-change: opacity;
					}

					&.controlDotActive::after {
						opacity: 1;
					}
				}

				.controlArrow {
					width: 24px;
					height: 24px;
					padding: 2px;

					&:active {
						transform: scale(0.95);
					}
				}
			}
		}

		.popupGallery {
			width: 100vw;
			height: 100vh;
			position: fixed;
			top: 0;
			left: 0;
			background-color: #000000cc;
			padding: 30px;
			z-index: 2000;
			opacity: 1;
			visibility: hidden;
			overflow: auto;
			transition: all .3s ease;

			@media (max-width: 768px) {
				padding: 15px;
			}

			@media (max-width: 576px) {
				padding: 10px;
			}

			.popupContent {
				width: 100%;
				height: 100%;
				position: absolute;
				top: 50%;
				left: 50%;
				display: table;
				overflow: hidden;
				position: relative;
				opacity: 0;
				transform: translate(-50%, -50%) scale(.2, .5);
				transition: all 1s ease .1s;

				.galleryItem {
					width: 100% !important;
					height: calc(100vh - 60px) !important;
					margin: 0;

					&:hover,
					&.bigbNowEditing {
						transform: scale(1);
					}
				}

				.galleryImages {
					img {
						object-fit: contain;
					}
				}
			}

			&.active {
				opacity: 1;
				visibility: visible;

				.popupContent {
					opacity: 1;
					transform: translate(-50%, -50%) scale(1);
					transition: all 1s ease .1s;
				}
			}
		}
	}
}